import React from "react";
import { Edition } from "../../../data";

import Calendar from "../pages/calendar";
import Details from "../pages/details";
import { WorkshopID } from "../types";

type WorkshopsProps = {
  search: any;
  edition: Edition;
};

const Workshops: React.FC<WorkshopsProps> = ({ edition, search }) => {
  const id = new URLSearchParams(search).get("id");

  return id ? (
    <Details edition={edition} id={id as WorkshopID} />
  ) : (
    <Calendar edition={edition} />
  );
};

export default Workshops;
