import React from "react";
import Balancer from "react-wrap-balancer";
import { Link } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { workshopCards } from "../res/workshopCards";
import Card from "react-bootstrap/Card";
import { Edition } from "../../../data";
import { getWorkshopDate } from "../../../hooks/useEditionData";
import { WorkshopDataType, WorkshopData, WorkshopID } from "../types";

import * as styles from "./workshops.module.css";

type WorkshopCardProps = {
  id: WorkshopID;
  edition: Edition;
};

const WorkshopCard: React.FC<WorkshopCardProps> = ({ id, edition }) => {
  const { t } = useTranslation();
  const details: WorkshopDataType = t(id, { returnObjects: true });
  const card = workshopCards[edition][id] as WorkshopData;

  return (
    <Link
      to={`?id=${id}`}
      key={"workshopCard" + edition + id}
      className={"link"}
    >
      <Card className={`card ${styles.card}`}>
        <Card.Header className={"cardHeader"}>
          <Balancer>
            {getWorkshopDate(edition, card.date)}, {card.time}
          </Balancer>
        </Card.Header>

        <Card.Body className={"cardBody"}>
          <Card.Title className={"cardTitle"}>
            <Balancer>{details.title}</Balancer>
          </Card.Title>
        </Card.Body>
      </Card>
    </Link>
  );
};

export default WorkshopCard;
