import React from "react";
import Balancer from "react-wrap-balancer";
import { workshopCards } from "../res/workshopCards";
import { useTranslation } from "react-i18next";
import { Edition } from "../../../data";
import { getWorkshopDate } from "../../../hooks/useEditionData";
import { WorkshopData, WorkshopDataType, WorkshopID } from "../types";

import * as styles from "./workshops.module.css";

type DetailsProps = {
  edition: Edition;
  id: WorkshopID;
};

const Details: React.FC<DetailsProps> = ({ edition, id }) => {
  const { t } = useTranslation();

  const card = workshopCards[edition][id] as WorkshopData;
  const workshops: WorkshopDataType = t(id, { returnObjects: true });

  return (
    <div className={`page ${styles.workshops}`} id={"workshops" + id}>
      <h2 className={"date"}>
        <Balancer>
          {getWorkshopDate(edition, card.date)}, {card.time}
        </Balancer>
      </h2>
      <h1 className={"title"}>
        <Balancer>{workshops.title}</Balancer>
      </h1>

      <p className={"p details"}>{workshops.description}</p>

      <iframe
        width={"70%"}
        height={"400"}
        src={card.link}
        title={workshops.title}
        allow={
          "accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        }
        allowFullScreen
      ></iframe>
    </div>
  );
};

export default Details;
