import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import Balancer from "react-wrap-balancer";
import { Button } from "react-bootstrap";
import { getDate } from "../../../hooks/useEditionData";
import { Edition } from "../../../data";
import { workshopCards } from "../res/workshopCards";
import WorkshopCard from "./workshopCard";
import { WorkshopDetailsType, WorkshopID } from "../types";

import * as styles from "./workshops.module.css";

type CalendarProps = {
  edition: Edition;
};

const Calendar: React.FC<CalendarProps> = ({ edition }) => {
  const { t } = useTranslation();

  const date = getDate(edition, "workshops");
  const details = t("details", {
    returnObjects: true,
  }) as WorkshopDetailsType[];
  const detailsAI = details[0].content as string[];

  return (
    <div className={`page ${styles.workshops}`} id={"workshops"}>
      <h2 className={"date"}>
        <Balancer>{date}</Balancer>
      </h2>
      <h1 className={"title"}>{t("title")}</h1>

      <div className={"details"}>
        <p className={`p ${styles.question}`}>{details[0].title}</p>
        <ol>
          {detailsAI.map((item, index) => (
            <li className={"p"} key={"detailsAI" + index}>
              {item}
            </li>
          ))}
        </ol>

        <p className={`p ${styles.question}`}>{details[1].title}</p>
        <p className={"p"}>{details[1].content}</p>

        <div className={styles.buttonLine}>
          <Button
            className={"buttonLink"}
            target={"_blank"}
            rel={"noreferrer"}
            href={
              "https://forms.zohopublic.eu/nitrolanguageprocessing/form/nscriereNitroVoluntari/formperma/LJbWawnZpfNAEgS3rEjbVgAo5gCDkOomXGHuyQxJyv8"
            }
          >
            {t("button")}
          </Button>
        </div>

        <p className={`p ${styles.question}`}>{details[2].title}</p>
        <ul className={`ul ${styles.cards}`}>
          {Object.keys(workshopCards[edition]).map((id) => (
            <WorkshopCard
              id={id as WorkshopID}
              edition={edition}
              key={`workshopCard${edition}${id}`}
            />
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Calendar;
